import React, { useState } from 'react';
import '../HomePage/HomePage.css'

function HomePage() {
    const [height, setHeight] = useState();
    const [weight, setWeight] = useState();
    function getBMI(height, weight) {
        var bmi = (weight / ((height * height) / 10000)).toFixed(2);
        document.getElementById('display').innerHTML = "BMI: " + bmi
    }
    return (
        <div className="main">
            <div className="container">
                <div className="display">
                    <p id="display"> </p>
                </div>
                <div className="height">
                    <label htmlFor="height">Height (in cm)</label>
                    <br />
                    <input type="number" required onChange={(e) => { setHeight(e.target.value) }} name="height" id="height" />
                </div>
                <div className="weight">
                    <label htmlFor="weight">Weight (in kg)</label>
                    <br />
                    <input type="number" required onChange={(e) => { setWeight(e.target.value) }} name="weight" id="weight" />
                </div>
                <div className="submit">
                    <input type="button" onClick={() => getBMI(height, weight)} name="submit" value="  GET BMI  " id="submit" />
                </div>
            </div>
        </div>
    )
}

export default HomePage
